import React from 'react';

import theme from '../../utils/theme'

const MobileList = (props) => {
  const barra = Object.assign({}, styles.barra, {backgroundColor: theme[props.theme].fourth});
  return (
    <>
    <div style={{...styles.container, backgroundColor: theme[props.theme].third, display: props.shown ? 'flex' : 'none' }} className="list-mobile">
      <li style={styles.item} >
        Sobre
      </li>
      <span style={barra} />
      <li style={styles.item}>
        Skills
      </li>
      <span style={barra} />
      <li style={styles.item}>
        Projetos
      </li>
      <span style={barra} />
      <li style={styles.item}>
        Contato
      </li>
    </div>

    <span style={{...styles.barra, width: '100%', height: '1px'}} />
    </>
  )
}
const styles = {
  container: {
    display: 'auto',
    listStyleType: 'none',
    flexDirection: 'column',
    justifyContent: 'space-around',
    paddingTop: '3%',
    paddingBottom: '3%',
    height: '15rem',
    alignItems: 'center'
  },
  item: {
    textAlign: 'center',
    cursor: 'pointer',
  },
  barra: {
    width: '90%',
    height: '3px',
    backgroundColor: '#fff',
    // marginRight: '10%'
  },

}
export default MobileList;